.paper {
  transform: none;
  transition: transform 0ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  box-shadow: rgb(9 30 66 / 8%) 0px 18px 28px;
  border-radius: 0;
  overflow-y: auto;
  height: auto;
  flex: 1 0 auto;
  z-index: 1200;
  position: fixed;
  outline: 0px;
  left: 0px;
  right: 0px;
  background-color: rgb(43, 47, 60);
  color: rgb(178, 183, 200);
  display: flex;
  flex-direction: column;
  top: 64px;
  max-height: calc(100% - 72px);
  width: 100vw;
}

.paper .list {
  list-style: none;
  margin: 0px;
  padding: 8px 0px;
  position: relative;
  background-color: rgb(43, 47, 60);
  border-radius: 0;
}

.paper .list.noPad {
  padding: 0;
}

.paper .list .listItemButton:hover {
  background-color: rgb(56, 61, 77);
  color: rgb(255, 255, 255);
  text-decoration: none;
}

.paper .list .listItemButton {
  -webkit-tap-highlight-color: transparent;
  background-color: transparent;
  outline: 0px;
  border-width: 0px 0px 1px;
  border-top-style: initial;
  border-right-style: initial;
  border-left-style: initial;
  border-image: initial;
  margin: 0px;
  border-radius: 0px;
  cursor: pointer;
  user-select: none;
  vertical-align: middle;
  appearance: none;
  -webkit-box-flex: 1;
  flex-grow: 1;
  -webkit-box-pack: start;
  justify-content: flex-start;
  position: relative;
  text-decoration: none;
  box-sizing: border-box;
  text-align: left;
  transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  border-bottom-style: solid;
  background-clip: padding-box;
  -webkit-box-align: center;
  align-items: center;
  border-color: rgb(63, 69, 90);
  display: flex;
  padding: 0 24px;
  color: rgb(255, 255, 255);
  min-height: 48px;
}

.paper .list .listItemButton .listItemIcon {
  flex-shrink: 0;
  display: inline-flex;
  color: inherit;
  min-width: 0%;
  margin-right: 8px;
}

.paper .list .listItemButton .listItemIcon .inactiveIcon {
  user-select: none;
  width: 1em;
  height: 1em;
  display: inline-block;
  fill: currentcolor;
  flex-shrink: 0;
  transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  font-size: 1.5rem;
  color: rgb(178, 183, 200);
}

.paper .list .listItemButton:hover .listItemIcon .inactiveIcon {
  color: inherit;
}

.paper .list .listItemButton .listItemIcon .activeIcon {
  user-select: none;
  width: 1em;
  height: 1em;
  display: inline-block;
  fill: currentcolor;
  flex-shrink: 0;
  transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  font-size: 1.5rem;
}

.paper .list .listItemButton .listItemIcon .activeText {
  flex: 1 1 auto;
  min-width: 0px;
  margin-top: 4px;
  margin-bottom: 4px;
}

.paper .list .listItemButton .listItemIcon .inactiveText {
  flex: 1 1 auto;
  min-width: 0px;
  margin-top: 4px;
  margin-bottom: 4px;
}

.paper .list .listItemButton .activeText .myTypography  {
  margin: 0px;
  font-weight: 400;
  line-height: 1.6;
  font-family: Inter, sans-serif;
  font-size: 0.85rem;
  display: block;
  color: white;
}

.paper .list .listItemButton .inactiveText .myTypography {
  margin: 0px;
  font-weight: 400;
  line-height: 1.6;
  font-family: Inter, sans-serif;
  font-size: 0.85rem;
  display: block;
  color: rgb(178, 183, 200);;
}

.paper .list .listItemButton:hover .inactiveText .myTypography  {
  color: white;
}

.paper .list .listItemButton .expandIcon {
  user-select: none;
  width: 1em;
  height: 1em;
  display: inline-block;
  fill: currentcolor;
  flex-shrink: 0;
  transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  font-size: 1.25rem;
  color: rgb(80, 97, 118);
}

.paper .list .collapse {
  height: auto;
  transition: height 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

.paper .list .listItemButton a {
  margin: 0px;
  font-weight: 400;
  line-height: 1.6;
  font-family: Inter, sans-serif;
  font-size: 0.85rem;
  display: block;
  color: inherit;
  text-decoration: none;
  width: 100%;
}

.paper .list .listItemButton a.navLink {
  color: rgb(178, 183, 200);
}

.paper .list .listItemButton:hover a.navLink {
  color: inherit;
}

.paper .list .listItemButton.activeListItemButton {
  background-color: rgb(56, 61, 77);
}